<template>
  <div class="Build">
    <back></back>
    <!--  -->
    <div class="title">
       webhooks 
    </div>
    <!--  -->
    <a-steps :current="current">
      <a-step v-for="item in steps" :key="item.title" :title="item.title" />
    </a-steps>
    <!--  -->
    <div class="steps-content-bg">
      <!--  1 -->
      <transition enter-active-class="fadeInLeft">
        <div v-if="current === 0" class="from-content">
          <a-form
            :form="form"
            :label-col="{ span: 5 }"
            :wrapper-col="{ span: 12 }"
            @submit="handleSubmit"
          >
            <a-form-item label="">
              <a-input
                v-decorator="[
                  'secret',
                  {
                    rules: [{ required: true, message: '!' }],
                  },
                ]"
                placeholder=""
              />
            </a-form-item>

            <a-form-item label="">
              <a-input
                v-decorator="[
                  'file',
                  {
                    rules: [{ required: true, message: '!' }],
                  },
                ]"
                placeholder=""
              />
            </a-form-item>

            <a-form-item label="CMD">
              <a-input
                v-decorator="[
                  'cmd',
                  {
                    rules: [{ required: true, message: 'CMD' }],
                  },
                ]"
                placeholder="  & "
              />
            </a-form-item>

            <a-form-item label="">
              <a-input
                v-decorator="[
                  'url',
                  {
                    rules: [{ required: true, message: '' }],
                  },
                ]"
                placeholder=""
              />
            </a-form-item>

            <a-form-item label="">
              <a-input
                v-decorator="[
                  'port',
                  {
                    rules: [{ required: true, message: '' }],
                  },
                ]"
                placeholder=""
              />
            </a-form-item>

            <a-form-item :wrapper-col="{ span: 12, offset: 5 }">
              <a-button type="primary" html-type="submit">
                
              </a-button>
            </a-form-item>
          </a-form>
        </div>
      </transition>
      <!--  2 -->
      <transition enter-active-class=" fadeInLeft">
        <!-- <transition name="test"> -->
        <div v-if="current === 1" class="download ">
          <div class="download-title">
             
          </div>
          <a-button type="primary" @click="download"> {{ code }} </a-button>
        </div>
      </transition>
      <!--  -->
      <transition enter-active-class="fadeInLeft">
        <div v-if="current === 2" class="server ">
          <p></p>
          <p></p>
          <p> <span class="red">npm install</span> </p>
          <p> <span class="red">node index.js</span> </p>
        </div>
      </transition>
    </div>
    <!--  -->
    <div class="steps-action">
      <!-- <a-button v-if="current < steps.length - 1" type="primary" @click="next">
        Next
      </a-button> -->
      <a-button v-if="current == steps.length - 1" type="primary" @click="back">
        
      </a-button>
      <a-button v-if="current > 0" style="margin-left: 8px" @click="prev">
        
      </a-button>
    </div>
  </div>
</template>
<script>
import Back from "@/components/common/Back.vue";
import axios from "axios";
import ejs from "ejs";
import ZIP from "jszip";
export default {
  layout: "basic",
  components: {
    Back,
  },
  data() {
    return {
      formLayout: "horizontal",
      form: this.$form.createForm(this, { name: "coordinated" }),
      current: 0,
      code: null,
      steps: [
        {
          title: "",
          content: "First-content",
        },
        {
          title: "",
          content: "Second-content",
        },
        {
          title: "",
          content: "Last-content",
        },
      ],
    };
  },
  methods: {
    next() {
      this.current++;
    },
    back() {
      this.$router.push("/");
    },
    prev() {
      this.current--;
    },
    async funDownload(content, filename) {
      var eleLink = document.createElement("a");
      eleLink.download = filename;
      eleLink.style.display = "none";
      // blob

      // 
      const zip = new ZIP();
      zip.file("package.json", content.packagejson);
      zip.file("index.js", content.indexjs);
      const result = await zip.generateAsync({
        type: "blob", // 
        compressionOptions: {
          level: 5, // 
        },
      });

      const blob = new Blob([result]);

      eleLink.href = URL.createObjectURL(blob);

      document.body.appendChild(eleLink); // 
      eleLink.click();

      document.body.removeChild(eleLink); // 
    },
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields(async (err, values) => {
        if (!err) {
          this.res = {};
          //  index.js 
          const indexjs = await axios.get("/webhooks-code-generator/template/new.ejs"); // 
          this.res.indexjs = ejs.render(indexjs.data, values); // 

          //  package.json 
          const packagejson = await axios.get("/webhooks-code-generator/template/package.ejs"); // 
          this.res.packagejson = JSON.stringify(packagejson.data);

          this.code = "";
          this.current++;
        }
      });
    },
    download() {
      //  
      this.funDownload(this.res, "DEMO.zip");

      this.current++;
    },
  },
};
</script>
<style scoped lang="scss">
.Build {
  width: 80%;
  margin: 30px auto;

  .title {
    font-weight: 300;
    font-size: 42px;
    color: #526488;
    word-spacing: 5px;
    text-align: center;
    margin-bottom: 30px;
  }

  // 
  .steps-content-bg {
    margin-top: 16px;
    border: 1px dashed #e9e9e9;
    border-radius: 6px;
    background-color: #fafafa;
    min-height: 450px;

    .from-content {
      width: 700px;

      margin: 40px auto;
    }

    // 
    .download {
      width: 300px;
      margin: 40px auto;
      text-align: center;

      .download-title {
        font-size: 20px;
        margin: 40px auto;
      }
    }

    .server {
      width: 400px;
      margin: 40px auto;

      p {
        font-size: 20px;
      }

      button {
        margin-top: 40px;
        float: right;
        margin-right: 20px;
      }
    }
  }

  .steps-action {
    margin-top: 24px;
  }
}
.red {
  color: red;
}
</style>
